* {
  margin: 0;
  padding: 0;
}
#main {
  width: 1920px;
  height: 1080px;
  background-color: #000;
}
.main-img {
  width: 530px;
  height: 750px;
  float: right;
  margin: 100px 100px 0 0;
  background-color: #5c5c5c;
}
.main-title {
  width: auto;
  height: auto;
}
.main-title ul {
  margin: 550px 0 0 100px;
}
.main-title li {
  font-size: 100px;
  color: #fff;
}
.main-title li:nth-child(3) {
  font-size: 50px;
}
