#project-wrap {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #000;
}

.project-content-container {
  display: flex;
  width: 100%;
  height: 72%;
  margin-top: 160px;
  overflow-x: hidden; /* 세로 스크롤 방지 */
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
}

.project-card-wrapper {
  flex: 0 0 100vw; /* 화면 100% 너비 */
  display: flex;
  justify-content: center;
  align-items: center;
  scroll-snap-align: center;
}

.project-card {
  width: 1400px; /* 카드 너비 */
  height: 100vh; /* 카드 높이 */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
}

.project-content-nav {
  width: 100%;
  height: 50px;
  display: flex;
  margin-top: -60px;
  justify-content: center;
}

.project-content-nav ul {
  display: flex;
  list-style: none;
  gap: 50px;
}

.project-content-nav ul li {
  color: #5c5c5c;
  font-size: 32px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.project-content-nav ul li:hover,
.project-content-nav ul li.active {
  color: #fff;
}
