#project-wrap {
  width: 100%;
  height: 1080px;
  background-color: #000;
}
.project-content {
  display: flex;           /* 이미 되어 있음 */
  flex-wrap: nowrap;       /* 한 줄로 유지 */
  overflow-x: hidden;
  overflow-y: hidden;      /* 좌우 스크롤 막고, transform으로 슬라이드 */
  scroll-behavior: smooth; /* 클릭 시 부드럽게 이동 */
  width: 1400px;             /* 브라우저 전체에 맞춤 */
  margin: 180px 0 0 200px;    /* 가운데 정렬 */
}
.project-content-img {
  width: 703px;
  height: 614px;
  overflow-y: hidden; 
  background-color: white;
}
.monitor {
  position: relative;
  width: 700px; /* 모니터 이미지 크기 */
  height: 564px;
  margin-top: 50px;
  overflow: hidden;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.screen-content {
  position: absolute;
  top: 5%; /* 모니터 안 화면 위치 조정 */
  left: 14%;
  width: 72%; /* 모니터 안 화면 크기 */
  height: 55%;
  object-fit: cover; /* 이미지 비율 유지하며 채움 */
  object-position: top;
  transition: transform 0.7s linear; /* 스크롤처럼 천천히 */
}
.screen-content:hover {
  animation: slideDown 2s linear forwards;
}
@keyframes slideDown {
  from {
    transform: translateY(0%);
  }
  to {
    transform: translateY(-50%); /* 이미지 높이에 따라 조절 */
  }
}
.project-content-text {
  width: 612px;
  height: 614px;
  overflow-y: hidden; 
  background-color: #000;
}
.project-content-text ul{
    display: flex;
}
.project-content-text ul li p{
  font-size: 50px;
  color: white;
}
.project-content-text ul .html-text p{
  color: #E34F26;
}
.project-content-text ul .css-text p{
  color: #1572B6;
}
.project-content-text ul .project-title{
  margin-bottom: 20px;
}
.project-content-text ul .project-title p{
  font-size: 40px;
  font-family: 'Fascinate';
}
.project-content-text ul .project-insert{
  margin: 0;
  padding: 0;
}
.project-content-text ul .project-insert p{
  font-size: 32px;
  line-height: 40px;
}
.project-content-text ul .project-insert p:nth-child(2){
  margin-bottom: 20px;
}
.project-content-text ul .project-insert p:nth-child(4){
  margin-bottom: 30px;
}
.project-content-text ul .project-insert .html-color{
  color: #E34F26;
}
.project-content-text ul .project-insert .css-color{
  color: #1572b6;
}






