#About-wrap{
    width: 1920px;
    height: 1080px;
}
.About-left{
    width: 422px;
    height: 710px;
    float: left;
    margin: 207px 0 0 112px;
    border-right: 2px solid #fff;
    overflow-y: hidden;
}
.About-img{
    width: 374px;
    height: 390px;
    background-color: #5c5c5c;
}
.About-left ul{
    margin-bottom: 20px;
}
.About-left ul:nth-child(5){
    margin-bottom: 0px;
}
.About-left li{
    color: #fff;
    font-size: 24px;
}
.About-left li:nth-child(1){
    font-size: 28px;
}
.About-right{
    width: 1278px;
    height: 710px;
    float: right;
    margin-top: 209px;
    margin-right: 81px;
}
.About-top-text{
    width: 100%;
    height: auto;
}
.About-top-text ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.About-top-text li {
  display: flex;
  justify-content: flex-start; /* 기본 좌측 정렬 */
  margin: 0;
}

/* 짝수번째 li 좌측 */
.About-top-text li:nth-child(odd) {
  justify-content: flex-start;
}
.About-top-text li:nth-child(3), .About-top-text li:nth-child(5) {
  margin-left: 210px;
}

/* 홀수번째 li 우측 */
.About-top-text li:nth-child(even) {
  justify-content: flex-end;
}
.About-top-text li p{
    font-size: 40px;
    line-height: 75px;
    color: #fff;
}




.About-botom-sub{
    width: 100%;
    height: 240px;
    margin-top: 20px;
}
.About-botom-sub ul{
    list-style: none;
    display: flex;
    float: left;
}
.About-botom-sub ul li{
    width: 406px;
    height: 240px;
    margin-right: 30px;
    border-top: 2px solid #fff;
    padding-top: 48px;
    font-size: 24px;
    color: #5c5c5c;
    cursor: pointer;
    transition: 0.3s ease-in-out
}
.About-botom-sub ul li:hover{
    color: #fff;
    transition: 0.3s ease-in-out;
}
.About-botom-sub ul li p{
    margin-top: 5px;
    cursor: pointer;
}

.training-p1{
    float: left;
}
.training-p2{
    float: left;
    padding-top: 40px;
    padding-right: 30px;
}
.training-p3{
    float: right;
}