#nav {
  font-family: "Zen Dots", "Fascinate", sans-serif;
  margin: 100px 0 0 112px;
  position: fixed;
  z-index: 100;
}

.nav-list {
  display: flex;
  gap: 30px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  color: #5c5c5c;   /* 기본 색상 */
  cursor: pointer;
  font-size: 32px;
  margin-right: 80px;
  transition: color 0.3s ease-in-out;
}

/* active 섹션일 때 색상 */
.nav-item.active {
  color: #fff;
}

/* hover 시 색상 */
.nav-item:hover {
  color: #fff;
}
