#skill-wrap{
    width: 1920px;
    height: 1080px;
    background-color: #000;
}
.skill-content{
    width: 1600px;
    height: 785px;
    margin: 180px 0 0 161px;
}
.skill-content ul{
    display: flex;
    list-style: none;
    float: left;
}
.skill-content ul li{
    width: 466px;
    height: 146px;
    display: flex;
    margin: 0 100px 50px 0;
    /* background-color: rgb(27, 74, 95); */
}
.skill-content li img {
  width: 146px;
  height: 146px;
  object-fit: contain;
  margin-right: 10px;
  flex-shrink: 0;   /* 부모가 좁아도 축소되지 않음 */
}
.content1 li div{
    width: 300px;
    height: 146px;
    margin: 0;
    padding: 0;
}
.content1 li div ul{
    display: flex;
    flex-direction: column;  /* li를 세로로 쌓기 */
    list-style: none;
    padding: 0;
    margin: 25px 0 0 0;
    overflow-y: hidden;
}
.content1 li div ul li{
    width: 100%;
    height: 30px;
    font-size: 32px;
    color: #fff;
    overflow-y: hidden;
    margin: 0 0 10px 0px;
    padding: 0;
    background-color: #000;
}
.content1 li div ul li:nth-child(3){
    min-width: 300px;
    height: 30px;
    border: 1px solid #fff;
    background-color: #000;
    border-radius: 20px;
}




